.footer {
   position: relative;
   padding: 162px 0 94px;
   background-color: #3727a3;
   color: #fff;
   @include f('dp400', 16);
   line-height: 1.2;
   overflow: hidden;
   @include md {
      padding: 160px 0 55px; }
   @include xs {
      padding: 130px 0 55px; }
   &__container {
      position: relative;
      z-index: 1;
      @include xs {
         text-align: center; } }
   &__row {
      justify-content: space-between; }
   &__col {
      padding: 0 15px;
      transform: translateX(100px);
      &:nth-child(1) {
         flex: 0 0 270px;
         @include r(1023) {
            flex: 0 1 50%;
            width: 50%; }
         @include xs {
            flex: 0 1 auto;
            width: 100%;
            margin-bottom: 30px; } }
      &:nth-child(2) {
         flex: 0 1 percentage(540 / 1230);
         margin-left: -30px;
         @include md {
            flex: 0 1 percentage(490 / 1230);
            margin-left: 0; }
         @include r(1023) {
            flex: 0 1 100%;
            width: 100%;
            order: -1;
            margin-bottom: 40px; }
         @include xs {
            margin-bottom: 30px; } }
      &:nth-child(3) {
         flex: 0 0 315px;
         margin-right: 15px;
         @include md {
            margin-right: 0; }
         @include r(1023) {
            flex: 0 1 50%;
            width: 50%;
            display: flex;
            flex-direction: column;
            align-items: center; }
         @include xs {
            flex: 0 1 auto;
            width: 100%; } } }
   &__title {
      margin-bottom: 30px;
      @include f('dp500');
      font-size: 20px;
      text-transform: uppercase;
      @include xs {
         margin-bottom: 15px; } }
   &__descr {
      &:not(:last-child) {
         margin-bottom: 15px; } }
   &__contacts {
      @include xs {
         display: flex;
         flex-direction: column; }
      &-tel {
         display: inline-block;
         margin-bottom: 3px;
         text-transform: uppercase;
         font-size: 30px; }
      &-mail {}
      &-address {
         margin-top: 20px;
         font-style: normal;
         @include xs {
            margin-top: 5px; } } }
   &__dev-link {
      display: block; }
   &__decor-rect {
      pointer-events: none;
      position: absolute;
      top: -152px;
      left: -65px;
      min-width: 1600px;
      z-index: -1;
      transform: translateX(200px);
      opacity: 0; }
   &__socials {
      margin-bottom: 20px; } }

