// *** begin screen *** //
.screen {
   position: relative;
   height: 100vh;
   background-color: #3b1eaa;
   @include cover;
   overflow: hidden;
   @include r(1599) {
      height: auto;
      padding: 180px 0 80px; }
   @include sm {
      padding: 130px 0 80px; }
   @include xs {
      padding: 80px 0 60px; }
   &__row {
      margin: 85px -15px 0;
      justify-content: space-between;
      @include r(1599) {
         margin: 0 -15px; }
      @include sm {
         flex-direction: column; } }
   &__col {
      &:nth-child(1) {
         flex: 0 1 percentage(550 / 1200);
         width: percentage(550 / 1200);
         padding-top: 30px;
         @include sm {
            flex: 0 1 auto;
            width: auto;
            margin-bottom: 50px; } }
      &:nth-child(2) {
         opacity: 0;
         transform: translateX(60px);
         flex: 0 1 percentage(615 / 1200);
         width: percentage(615 / 1200);
         @include sm {
            flex: 0 1 auto;
            width: 630px;
            margin: 0 auto; }
         @include xs {
            width: auto; } } }
   &__container {
      position: relative;
      z-index: 1;
      display: flex;
      align-items: center;
      height: 100%; }
   &__text {}
   &__title {
      margin-bottom: 30px;
      margin-left: -5px; }
   &__descr {
      margin-bottom: 48px;
      @include f('dp400', 30, 40);
      color: #fff;
      @include sm {
         max-width: 550px; }
      @include xs {
         margin-bottom: 20px;
         font-size: 20px;
         line-height: 28px; }
      &-currency {
         @include f('rob400'); } }
   &__section-label {
      transform: translate(-50.5%,-46%); }
   &__decor-rect {
      position: absolute;
      &_1 {
         top: -63%;
         width: 90%;
         height: 100%;
         left: -23%;
         opacity: .5;
         z-index: 1;
         filter: drop-shadow(0 0 120px rgba(107, 200, 248, 0.24)); }
      &_2 {
         top: -55%;
         width: 120%;
         height: 120%;
         left: -23%;
         opacity: 0.7;
         filter: drop-shadow(0 0 120px rgba(107, 200, 248, 0.24)); }
      &_3 {
         bottom: -55%;
         left: -25%;
         width: 80%;
         height: 80%;
         opacity: 0.35; } }
   &__btn {
      @include xs {
         width: 270px;
         display: block;
         margin: 0 auto; } } }

// end screen //

// *** begin quotations *** //
.quotations {
   position: relative;
   padding: 70px 0 80px;
   background-image: linear-gradient(-28deg, #5934a2 0%, #dc4a33 100%);
   @include xs {
      padding: 50px 0; }
   &__section-label {
      transform: translate(-50%,-23%); }
   &__section-title {
      margin-bottom: 62px;
      @include xs {
         margin-bottom: 30px; }
      .section-title__icon {
         margin: 0 0 20px;
         @include xs {
            margin: 0 0 -10px; } } }
   &__container {
      position: relative;
      z-index: 3; }
   &__chart-wrap {
      @include xs {
         overflow: auto; } } }
// end quotations //

// *** begin start *** //
.start {
   position: relative;
   padding: 127px 0 110px;
   background-color: #3b1eaa;
   overflow: hidden;
   @include xs {
      padding: 60px 0; }
   &__container {
      position: relative;
      z-index: 3; }
   &__row {
      justify-content: space-between;
      @include sm {
         flex-direction: column; } }
   &__col {
      &:nth-child(1) {
         flex: 0 1 42%;
         @include md {
            flex: 1 1 42%; }
         @include sm {
            flex: 0 1 auto;
            margin-bottom: 80px; } }
      &:nth-child(2) {
         flex: 0 1 percentage(615 / 1200);
         @include sm {
            flex: 0 1 auto; } } }
   &__title {
      margin-left: -7px;
      margin-bottom: 50px;
      @include xs {
         margin-left: 0;
         margin-bottom: 30px; } }
   &__descr {
      margin-bottom: 50px;
      color: #fff;
      &-text {
         &:not(:last-child) {
            margin-bottom: 30px; } } }
   &__user {
      position: absolute;
      @include xs {
         display: none; }
      &_1 {
         top: 0;
         left: 33px; }
      &_2 {
         left: 50%;
         bottom: 0;
         margin-left: -197px; }

      &_3 {
         top: 32%;
         right: -1.6%;
         @include sm {
            top: 35%; } } }
   &__decor-rect {
      position: absolute;
      top: -94%;
      left: -12%;
      z-index: 0;
      opacity: .6; }
   &__section-label {
      transform: translate(-50%,-27%); }
   &__btn {
      @include xs {
         display: block;
         width: 270px;
         margin: 0 auto; } } }
// end start //

// *** begin earn *** //
.earn {
   position: relative;
   padding: 105px 0 135px;
   @include cover;
   @include xs {
      padding: 50px 0; }
   &::after {
      @include coverdiv;
      background-color: rgba(#000, .75);
      content: ''; }
   &__container {
      position: relative;
      z-index: 3; }
   &__row {
      flex-wrap: nowrap;
      justify-content: space-between;
      @include sm {
         flex-direction: column; } }
   &__col {
      padding: 0 15px;
      &:nth-child(1) {
         flex: 0 1 51%;
         @include sm {
            flex: 0 1  auto;
            margin-bottom: 40px; } }
      &:nth-child(2) {
         flex: 0 0 565px;
         margin-right: -20px;
         @include md {
            margin-right: 0; }
         @include sm {
            flex: 0 1 auto;
            max-width: 650px;
            margin: 0 auto; } } }
   &__title {
      margin-left: -7px;
      margin-bottom: 50px;
      opacity: 0;
      @include xs {
         margin-left: 0;
         margin-bottom: 25px; } }
   &__descr {
      max-width: 485px;
      margin-bottom: 48px;
      @include sm {
         max-width: none; }
      @include xs {
         margin-bottom: 30px; }
      &-text {
         &:not(:last-child) {
            margin-bottom: 30px;
            @include xs {
               margin-bottom: 15px; } } } }
   &__stat {
      margin-top: 18px;
      opacity: 0;
      transform: translateX(60px);
      @include xs {
         margin-top: 0; } }
   &__btn {
      @include xs {
         display: block;
         width: 270px;
         margin: 0 auto; } } }

// end earn //

// *** begin partner *** //
.partner {
   position: relative;
   padding: 67px 0 84px;
   @include cover;
   @include xs {
      padding: 50px 0; }
   &::after {
      opacity: .95;
      @include coverdiv;
      background-image: linear-gradient(to bottom right, #ce462c, #f8af2f);
      content: ''; }
   &__container {
      position: relative;
      z-index: 1; }
   &__section-title {
      margin-bottom: 55px;
      @include xs {
         margin-bottom: 25px; } }
   &__slider {
      @include sm {
         max-width: 615px;
         margin: 0 auto; }
      .slick-list {
         margin: 0 -15px; }
      .slider-dots {
         margin-top: 40px;
         @include xs {
            margin-top: 10px; } }
      &-item {
         padding: 15px;
         color: #fff;
         &-inner {
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            min-height: 452px;
            height: 100%;
            padding: 25px 30px 83px 40px;
            box-shadow: 0 0 27px 1px rgba(226, 75, 46, 0.24);
            border-radius: 10px;
            background-image: linear-gradient(-49deg, #e24b2e 0%, #e24b2e 100%);
            @include xs {
               padding: 15px; } } } }
   &__item {
      &:not(:last-child) {
         margin-bottom: 30px; }
      &-title {
         margin-bottom: 11px;
         @include f('dp500', 23, 40);
         @include xs {
            font-size: 21px;
            line-height: 30px; } }
      &-descr {
         font-size: 16px;
         line-height: 23px;
         @include f('dp400'); } } }
// end partner //

// *** begin news *** //
.news {
   position: relative;
   padding: 85px 0 90px;
   background-image: linear-gradient(-28deg, #3b1eaa 0%, #5934a2 100%);
   overflow: hidden;
   @include xs {
      padding: 50px 0 35px; }
   &__decor-rect {
      position: absolute;
      z-index: 0;
      opacity: .4;
      &_1 {
         top: -57%;
         left: -23.4%; }
      &_2 {
         bottom: -88%;
         right: -14%; }
      &_3 {
         bottom: -75%;
         right: 1%; } }
   &__container {
      position: relative;
      z-index: 1;
      max-width: 1200px; }
   &__section-title {
      margin-bottom: 40px;
      @include xs {
         margin-bottom: 25px; } }
   &__slider {
      @include md {
         padding: 0 50px; }
      @include xs {
         padding: 0; }
      .slick-list {
         margin: 0 -15px;
         padding: 15px 0; }
      .slider-btn {
         &_prev {
            left: -80px;
            @include md {
               left: 0; } }
         &_next {
            right: -80px;
            @include md {
               right: 0; }
            svg {
               transform: scaleX(-1); } } } }
   &__item {
      display: block;
      margin: 0 15px;
      color: #fff;
      transition: .3s ease-in-out;
      &:hover {
         transform: translateY(-10px);
         @include xs {
            transform: none; } }
      &-img-wrap {
         margin-bottom: 17px;
         overflow: hidden;
         border-radius: 15px;
         @include ratio(370, 267);
         @include cover-img;
         img {
            @include coverdiv; } }

      &-content {
         display: flex;
         align-items: flex-start;
         font-size: 16px;
         line-height: 22px; }
      &-text {
         flex: 0 1 calc(100% - 70px);
         padding-right: 30px;
         font-size: 16px;
         line-height: 22px;
         @include md {
            padding-right: 20px; }
         @include xs {
            font-size: 15px;
            line-height: 20px; } }
      &-date {
         flex: 0 0 70px;
         width: 70px;
         margin-top: 5px;
         padding: 5px;
         border-radius: 10px;
         border: 1px solid #eaa12e;
         text-align: center;
         span {
            display: block;
            color: #eaa12e; } } } }
// end news //

// *** begin begin *** //
.begin {
   padding: 87px 0 0;
   background-color: #3727a3;
   @include xs {
      padding: 60px 0 0; }
   .section-title__text {
      font-size: 50px;
      line-height: 40px;
      @include xs {
         font-size: 32px;
         line-height: 40px; } }
   &__container {
      max-width: 1200px; }

   &__top {
      &-text {
         margin-bottom: 65px;
         text-align: center;
         @include xs {
            margin-bottom: 30px; } }
      &-descr {
         max-width: 650px;
         margin: 34px auto 0;
         @include f('dp400', 23, 28);
         color: #fff;
         @include xs {
            font-size: 18px;
            line-height: 24px;
            margin: 20px auto 0; }
         br {
            @include xs {
               display: none; } } } }
   &__form {
      &-row {
         @include xs {
            flex-direction: column; } }
      &-field {
         &:not(:last-child) {
            margin-bottom: 25px;
            @include xs {
               margin-bottom: 20px; } } }
      &-col {
         &:nth-child(1) {
            opacity: 0;
            transform: translateX(-60px); }
         &:nth-child(2) {
            opacity: 0;
            transform: translateX(60px); }
         &:not(:last-child) {
            @include xs {
               margin-bottom: 20px; } } }
      &-submit {} }
   &__actions {
      max-width: 1030px;
      margin: 30px auto 0;
      text-align: center;
      &-agree {
         margin-bottom: 20px;
         @include f('dp400', 13, 28);
         color: #fff;
         @include xs {
            line-height: 22px; } } } }

// end begin //
